@media #{$small-up}{

  .page-node-resume{

    #resume-presentation{
      min-height: 85vh;
      background: $black;
    }

    .panel{
      width: 100%;
      height: 100%;
      min-height: 0;
      max-height: 0;
      padding: 0 1.5em;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: $white;
      position: relative;
      overflow: hidden;

      &.visible{
        padding: 1.5em;
        min-height: 85vh;
        max-height: 100%;
        transition: .5s all ease-out;
      }

      p{
        font-size: .8rem;
        padding: 0 0 1em;
        text-align: justify;

        &:last-of-type{
          padding: 0;
        }
      }

      ul{
        margin: 0;
        list-style: none;
        line-height: 1.8;

        .highlighted{
          font-weight: 900;
        }
      }

      h2{
        width: 100%;
        font-size: 1.5rem;
        padding: 0 0 .5em;

        a{
          color: $white;
          @include link($white, $white);
        }
      }
    }

    .panel-container{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      max-width: 825px;

      &.slides{
        display: inline-block;
        margin: 0 auto;
      }
    }

    .panel-cta{
      margin: 2em 0 0;

      a{
        @include link-square;
        margin: 0;
      }
    }

    .background-video{
      position: absolute;
      width: 100%;
      min-height: 100%;
      z-index: -1;

      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }

      video{
        min-width: 100%;
        min-height: 100%;
      }
    }

    #intro,
    #outro{

      .panel-container{
        flex-direction: column;
        font-size: 7vw;

        p{
          opacity: .5;
          padding: .5em 0;
        }

        a{
          @include link-square;
        }
      }
    }

    #overview{
      background: $cyan;
    }

    #skillsets{
      background: $blue;
    }

    #education{
      background: $violet;
    }

    #job-hathway{
      .background-video{
        &:before{
          background: rgba(175, 193, 36, 0.95);
        }
      }
    }

    #job-accuair{
      .background-video{
        &:before{
          background: rgba(0, 0, 0, 0.85);
        }
      }
    }

    #job-imrc{
      .background-video{
        &:before{
          background: rgba(80, 45, 14, 0.9);
        }
      }
    }

    #job-hathway,
    #job-accuair,
    #job-imrc{
      z-index: 10;
    }

    .job-stats{

      p{
        padding: 0;

        &:last-child{
          padding: 1em 0 0;
        }
      }
    }
  }
}

@media #{$medium-up}{

  .page-node-resume{

    .panel{
      padding: 0 2em;

      &.visible{
        padding: 2em;
      }

      p{
        font-size: 1rem;
        text-align: left;
      }

      h2{
        width: auto;
        font-size: 2rem;
        padding: 0 0 .25em;
      }
    }

    #intro,
    #outro{

      .panel-container{
        flex-direction: row;
        font-size: 3vw;

        p{
          padding: 0;
        }
      }
    }
  }
}

@media #{$large-up}{

  .page-node-resume{

    #intro,
    #outro{

      .panel-container{
        font-size: 2rem;
      }
    }

    .panel{

      h2{
        font-size: 2.5rem;
      }
    }

    .panel-cta{
      font-size: 1.15em;
    }
  }
}
